body {
    background: #1a1a1a;
    color: white;
}

 .App {
      
    background: #303c52;
    color: white;
}

.header {
    background: url('../images/home-bg.jpg');
    background-size: cover;
    background-position: center;
    height: 600px;
    color:#fff;
    opacity: 0.4;
}
.header  {
    color: white;
    box-sizing: border-box;
    text-shadow: 0 0 12px black;
    opacity: 1;
    text-shadow: 0 0 12px black;
}
.media-body p{
    box-sizing: border-box;
}

#home {
    display: flex;
    text-align: center;
    align-items: center;
    padding-top: 120px;
    height: 518px;
}


h1 {

    text-transform: uppercase;
    text-align: center;
}

section h1 
{
    font-size: 22px;
}

section h2 
{
    font-size: 18px;
}

section {
    padding-top: 45px;
    padding-bottom: 45px;
}
.media 
{
    /* padding-top: 45px; */
}

.media-col , .card
{
    padding-top: 22px;
    padding-bottom: 18px;
}
.media-col 
{
    display: flex;
    /* align-items: center; */
}
.media-col h2{
    padding-bottom: 12px;
}

.media-object, .media-body
{
    vertical-align: top;
    display: inline-block;
}
.media-body {
    padding-left: 15px;
    margin-left: 5px;
    margin-top: -5px;
}

.php-image img, .symfony-image img,.laravel-image img{
    width: 66px;
    height: 66px;
    padding: 12px;
    border-radius: 85px;
    border: 2px solid;
}
.php-image {
    /* background-image: url(../images/php-logo.png); */
    /* width: 144px; */
    background-size: contain;
    background-repeat: no-repeat;
    background-position-y: center;
    padding-right: 17px;
}
.laravel-image {
    /* background-image: url(../images/laravel-logo.png); */
    /* width: 144px; */
    background-size: contain;
    background-repeat: no-repeat;
    background-position-y: center;
}
.symfony-image {
    /* background-image: url(../images/symfony-logo.png); */
    /* width: 144px; */
    background-size: contain;
    background-repeat: no-repeat;
    background-position-y: center;
}

/********************************************************************************************/

.title {
    min-height: 63px;
}


/******************************** ABOUT *******************************************************/
#about {
    background: #0c0b0b;
}

#competencies-levels span small{
    float: right;
}

#about .progress .progress-bar-danger {
    background: #D43F52;
}

#about .progress {
    height: 6px;
    margin-top: 5px;
}

/***************************************************************************************/
/******************************** CONTACT *******************************************************/

#section_contact {
    background: #0c0b0b;
}

/***************************************************************************************/
.form-data input, .form-data textarea{
    width: 100%;
    padding-left: 12px;
    padding-top: 2px;
    padding-bottom: 2px;
    color: #3a3838;
    font-size: 18px;
    font-weight: 200;
}

.form-data input {
    height: 45px;
}

.form-data textarea {
   min-height: 95px;
   min-width: 100%;
   max-width: 100%;
   max-height: 1000px;
}

/*********************/
.fa {
    border: 1px solid;
    padding: 15px;
    margin-right: 5px;
    border-radius: 23px;
    width: 45px;
    height: 45px;
    text-align: center;
}

/************************************************/

 .mycard {
    position: relative;
    display: flex;
    flex-direction: column;
    min-width: 0;
    word-wrap: break-word;
    background-clip: border-box;
    border-radius: .25rem;
    color: white;
}


.App .media .title{
    display: block;
}

section {
    display: block;
}

section .title{
    display: block;
}

section h1 
{
    border-bottom: 2px dashed;
    padding-bottom: 17px;
    color: white;
}

.App {
    background: #1a1a1a;
}


.profile {
    position: relative;
    min-height: 237px;
}

.profile img 
{
    position: absolute;
    right: 0;
}